:root{
  --color-bg:  #141212;
  --color-bg-variant: #000000;
  --color-primary: #b90000;
  --color-primary-variant: rgba(236, 198, 198, 0.6);
  --color-white:#ffffff;
  --color-light: #d29696;
  
  --transition: all 400ms ease;
  --transition2: all 1000ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html{
  scroll-behavior: smooth;
}
::-webkit-scrollbar{
  display: none;
}

body{

  font-family: 'Poppins', sans-serif;
  background: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7;
  background-image: url(../src/assets/bg-texture.png);
  transition: var(--transition2);

}


.container{
  width: var(--container-width-lg); 
  margin: 0 auto;
}


h1,h2,h3,h4,h5{
font-weight: 500;
}

h1{
  font-size: 2.5rem;
}
section{
  margin-top: 8rem;
}
section > h2, 
section > h5{
  text-align: center;
  color: var(--color-light);
}

section >h2{
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light{
  color: var(--color-light);
}

a{
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover{
  color:var(--color-white);
}
.btn{
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: .4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);


}

.btn:hover{
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary{
  background: var(--color-primary);
  color: var(--color-bg);
}

img{
  display: block;
  width: 100%;
  object-fit: cover;
}


/*MEdia Queries*/

@media screen and (max-width: 1024px){
  .container{
       width: var(--container-width-md); 
  }

  section {
    margin-top: 6rem;

  }
}

/*MEdia Queries small*/

@media screen and (max-width: 600px){
  .container{
       width: var(--container-width-sm); 
  }

  section > h2 {
    margin-top: 2rem;
    
  }
}

.fade-enter{
  opacity: 0;
}
.fade-exit{
  opacity: 1;
}
.fade-enter-active{
  opacity: 1;
}
.fade-exit-active{
  opacity: 0;
}
.fade-enter-active,
.fade-exit-active{
  transition: opacity 500ms;
}