header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{

    text-align: center;
    height: 100%;
    position: relative;
}

/*------------------CTA----------*/

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/*--------social--------*/

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: .8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/*------------------------ me ---------*/
.me{
    background:linear-gradient(var(--color-primary),transparent);
    width: 22rem;
    height: 25rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    overflow: hidden;
    border-radius: 12rem 12rem 0 0;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}


.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: .9rem;
}

@media screen and (max-width: 1024px) {
    header{
        height: 68vh;
    }

    .me{
       
        width: 20rem;
        height: 25rem;
        position: absolute;
        left: calc(50% - 10rem);
        margin-top: 1rem;
        border-radius: 11rem 11rem 0 0;
        padding: 5rem 1.5rem 1.5rem 1.5rem;
    }
    
}

@media screen and (max-width: 600px) {
    header{
        height: 100vh;
    }
    .header__socials, .scroll__down{
        display: none;
    }

    .me{
       
        width: 14rem;
        height: 18rem;
        position: absolute;
        left: calc(50% - 7rem);
        margin-top: 1rem;
        border-radius: 8rem 8rem 0 0;
        padding: 5rem 1.5rem 1.5rem 1.5rem;
    }
}